<template>
     <div class="input-group">
        <div class="input-group-btn">
            <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <i class="fa fa-caret-down"></i>
            </button>
            <ul class="dropdown-menu">
                <li v-for="(o,index) in options" :key="index">
                    <a href="javascript:;" v-text="o" v-on:click.prevent="itemSelected(o)"></a>
                </li>
            </ul>
        </div>
        <input type="text"
          :name="name"
          :required="required"
         class="form-control" v-model="localValue" v-bind="$props" @input="onInput" @change="onChange"/>
    </div>
</template>

<script>
export default {
  name: "bs-input-select",
  props: {
    required: Boolean,
    name: String,
    value: String,
    options: Object
  },
  data() {
    return {
      localValue: this.value
    };
  },
  watch: {
    value(value) {
      this.localValue = this.value;
    }
  },
  created() {
    console.log(this.$props);
  },
  methods: {
    itemSelected(value) {
      this.localValue = value;
      this.$emit("input", value);
      this.$emit("change", value);
    },
    onInput(evt) {
      this.localValue = evt.target.value;
      this.$emit("input", evt.target.value, evt);
    },
    onChange(evt) {
      this.localValue = evt.target.value;
      this.$emit("change", evt.target.value, evt);
    }
  }
};
</script>
