<template>
    <input :type="type" class="form-control" v-bind="$props" v-model="localValue" @input="onInput"  @change="onChange" />
</template>

<script>
export default {
  name: "bs-input",
  props: {
    required: Boolean,
    value: String,
    type: {
      type: String,
      default: "text"
    }
  },
  watch: {
    value(value) {
      this.localValue = this.value;
    }
  },
  data() {
    return {
      localValue: this.value
    };
  },
  methods: {
    onInput(evt) {
      if (evt.target.composing) return;
      this.localValue = evt.target.value;
      this.$emit("input", evt.target.value, evt);
    },
    onChange(evt) {
      if (evt.target.composing) return;
      this.localValue = evt.target.value;
      this.$emit("change", evt.target.value, evt);
    }
  }
};
</script>
